import React from "react";
import { Link, graphql } from "gatsby";
import Img from "gatsby-image";

import Layout from "../components/layout";
// import Image from "../components/image";
import SEO from "../components/seo";
// import Sketch from "../components/sketch";

const IndexPage = ({ data }) => {
  const IndexPageImage = ({ path }) => {
    const image = data.images.edges.filter(
      ({ node }) =>
        node.relativePath ===
        path.replace("/sketch/", "").replace(/\/$/, "") + ".png"
    );

    if (image) {
      return <Img fluid={image[0].node.childImageSharp.fluid} alt={path} />;
    }
  };

  const IndexPageLink = ({ path }) => {
    return (
      <Link to={path}>
        <IndexPageImage path={path} />
      </Link>
    );
  };

  return (
    <Layout>
      <SEO title="Home" />
      <h1>Hi people</h1>
      {data.sketches.edges.map(({ node }) => (
        <IndexPageLink key={node.path} path={node.path} />
      ))}
    </Layout>
  );
};

export default IndexPage;

export const query = graphql`
  query Index {
    sketches: allSitePage(
      filter: { path: { regex: "/sketch/" } }
      sort: { order: DESC, fields: path }
    ) {
      edges {
        node {
          path
        }
      }
    }
    images: allFile(
      filter: { relativePath: { regex: "/[0-9]{4}/" }, ext: { in: [".png"] } }
      sort: { fields: relativePath, order: DESC }
    ) {
      edges {
        node {
          relativePath
          childImageSharp {
            fluid(maxWidth: 400) {
              ...GatsbyImageSharpFluid
            }
            twitter: resize(width: 1024) {
              src
            }
            og: resize(width: 1200, height: 630, cropFocus: CENTER) {
              src
            }
          }
        }
      }
    }
  }
`;
